import COUNTRIES from "./CITIES";
import { EMAIL_REGEX, NUMBER_REGEX } from "./regex";

export const isNumber = (value: number | string) => NUMBER_REGEX.test(value.toString());

export const isEmail = (value: string) => EMAIL_REGEX.test(value.toString());

export const isPhoneNumber = (value: string, code: string) => {
    const regex = COUNTRIES.find(x => x.code === code)?.regex;
    return regex?.test(value);
};
