import { UserInput } from '../../types/UserInput';

export type EntryProps = UserInput<HTMLInputElement> & {
    value: string | number;
    placeholder?: string;
    type?: 'password' | 'text';
    autoFocus?: boolean;
    errorMessage?: string;
    required?: boolean;
    onFocusSpeechText?: string;
}

const Entry = ({ label = '', autoFocus = false, required = false, onFocusSpeechText = '',
    errorMessage = '', type = 'text', value = '', styleClass = '', name = '', id, onChange, placeholder = '' }: EntryProps) => {
    return (
        <>
            <div data-testid={'entry-container'} className={`d-flex flex-column ${styleClass}`}>
                {label && <label className={"mb-2 fw-bold fs-875em"} data-testid={'entry-label'} htmlFor={id}>{label}</label>}
                <input
                    id={id}
                    type={type}
                    name={name || id}
                    value={value}
                    aria-labelledby={onFocusSpeechText}
                    aria-required={required}
                    placeholder={placeholder}
                    autoFocus={autoFocus}
                    data-testid={'entry-input'}
                    className={`p-3 shadow-none border rounded entry-input ${errorMessage && 'errorBorder'}`}
                    onChange={onChange}
                />
            </div>
            {errorMessage && <span className='d-block mt-1 fs-875em text-danger'>{errorMessage}</span>}
        </>
    )
}

export default Entry
