import Dropdown, { DropdownProps } from "../Dropdown/Dropdown";
import Entry, { EntryProps } from "../Entry/Entry";
import styles from './PhoneInput.module.scss';

type PhoneInputProps = Omit<DropdownProps & EntryProps & {
    onNumberChange: EntryProps['onChange'];
    onCountryCodeSelection: DropdownProps['onChange'];
    errorMessage?: string;
}, 'onChange'>;

const PhoneInput = ({ id, label, errorMessage = '', options, value, placeholder = '', onNumberChange, onCountryCodeSelection }: PhoneInputProps) => {

    return (
        <>
            <div className="container m-0 p-0">
                <label className="fw-bold fs-875em mb-2" htmlFor={id}>{label}</label>
                <div className={styles.phoneNumberContainer}>
                    <Dropdown
                        id={`${id}-dropdown`}
                        styleClass={`${styles.phoneNumberDropdown} ${errorMessage && styles.errorBorder}`}
                        options={options}
                        onChange={onCountryCodeSelection}
                    />
                    <Entry
                        id={`${id}-entry`}
                        value={value}
                        placeholder={placeholder}
                        styleClass={`${styles.phoneNumberEntry} ${errorMessage && styles.errorBorder}`}
                        onChange={onNumberChange}
                    />
                </div>
                {errorMessage && <span className='d-block mt-1 fs-875em text-danger'>{errorMessage}</span>}
            </div>
        </>
    )
}

export default PhoneInput
