import { ChangeEvent, ReactNode } from 'react';
import styles from './Checkbox.module.scss';

interface CheckboxProps {
    id: string;
    name?: string;
    children?: ReactNode | string;
    styleClass?: string;
    showError?: boolean
    onSelect: (e: ChangeEvent<HTMLInputElement>) => void
}

const Checkbox = ({ name, id, showError = false, children = '', styleClass = '', onSelect }: CheckboxProps): JSX.Element => {
    return (
        <>
            <div className={`row mx-0 p-0 align-items-center ${styles.checkboxContainer} ${styleClass}`}>
                <input className={`mr-2 ${showError ? 'errorBorder' : ''}`} type="checkbox" name={name || id} id={id} onChange={onSelect} />
                <label className={'col-11 p-0 m-0'} htmlFor={id}>{children}</label>
            </div>
        </>
    )
}

export default Checkbox
