import { Option } from '../../types/Option';
import { UserInput } from '../../types/UserInput';

export type DropdownProps = UserInput<HTMLSelectElement> & {
    options: Array<Option>
}

const Dropdown = ({ id, name = '', label = '', options, styleClass = '', onChange }: DropdownProps) => {
    const renderOptions = () => {
        return options.map(({ label, value }, i) => {
            return <option key={`${label}-${i}`} value={value}>{label}</option>
        });
    }

    return (
        <div className={`d-flex flex-column dropdown-container ${styleClass}`} data-testid={'dropdown-container'}>
            {label && <label className={'mb-2 fw-bold fs-875em'} htmlFor={id} data-testid={'dropdown-label'}>{label}</label>}
            <select
                id={id}
                name={name}
                className={'p-3 rounded'}
                data-testid={'dropdown-select'}
                onChange={onChange}
            >
                {renderOptions()}
            </select>
        </div>
    )
}

export default Dropdown
