import { MouseEvent, ReactNode } from "react";

type ButtonProps = {
    children: ReactNode,
    type: 'primary' | 'invisible'
    styleClass?: string,
    onClick: (e: MouseEvent<HTMLButtonElement>) => void
}

const Button = ({ children, styleClass = '', type, onClick }: ButtonProps): JSX.Element => {
    return (
        <button
            type="button"
            className={`p-2 m-0 shadow-none border-0 rounded-pill text-center text-white ${type === 'primary' ? 'bgc-primary' : ''} ${styleClass}`}
            onClick={onClick}
            data-testid="button">
            {children}
        </button>
    )
}

export default Button
